<template>
  <div>
    <Lunbo />
    <div class="g-center">
      <div class="g-nav flex">
        <span class="iconfont icon-information"></span>
        <h3>{{bd_news.category_title}}</h3>
        <div class="time-nav">
          <a :class="{'b-active' : news_index===index}" href="#" @click="$refs.hero.swiper.slideTo(index)" v-for="(list,index) in bd_news.news_list" :key="index">{{list.news_type}}</a>
          <!-- <a :class="{'b-active' : news_index===1}" href="#" @click="$refs.hero.swiper.slideTo(1)">周</a>
          <a :class="{'b-active' : news_index===2}" href="#" @click="$refs.hero.swiper.slideTo(2)">月</a> -->
        </div>
      </div>
      <div class="hot-list">
        <swiper
          :options="{autoHeight:true,onSlideChangeEnd:function(){news_index = $refs.hero.swiper.realIndex}}"
          ref="hero"
        >
          <swiper-slide v-for="(list,index) in bd_news.news_list" :key="index">
            <ul v-for="(item,i) in list.type_news_list" :key="i">
              <li class="list-1 flex"  v-if="i===0">
                <div class="hot-img" >
                  <img :src="item.news_img" alt />
                  <span class="iconfont icon-bofang"></span>
                </div>
                <div class="hot-title">
                  <span class="hot-l">{{item.news_title}}</span>
                  <div class="flex hot-r">
                    <div>
                      <span class="iconfont icon-shipin"></span>
                      <span>{{item.news_num}}</span>
                    </div>
                    <div>
                      <span class="iconfont icon-shijian"></span>
                      <span>{{item.news_date}}</span>
                    </div>
                  </div>
                </div>
              </li>
              <li class="list-2 flex" v-else>
                <div class="list-2-r ellipsis">{{item.news_title}}</div>
                <div class="list-2-l">
                  <span class="iconfont icon-shipin"></span>
                  <span>{{item.news_num}}</span>
                </div>
              </li>
            
            </ul>
          </swiper-slide>
        </swiper>
      </div>
      <div class="hot-btn" @click="changeBtn()">
          {{btn_t?"查看完整榜单":"收起榜单"}}         
        </div>
    </div>
  </div>
</template>
<script>
import bd_news from '../data/bd_news.json'
import Lunbo from "../components/Lunbo.vue";
export default {  
  data() {
    return {
      news_index: 0,
      active: 0,
      btn_t:true,
      bd_news:{}
    }
  },
  methods: {
    changeIndex() {
      this.news_index = this.$refs.hero.swiper.realIndex;
    },
     changeBtn() {
      this.btn_t = !this.btn_t;
    }
  },
   mounted() { 
    this.$nextTick(()=>{
       this.bd_news=bd_news[0]      
    })      
  },
  components: {
    Lunbo
  }
};
</script>
<style lang="scss" >
.g-center {
  width: 100%;
  margin-top: 0.7692rem;
  background: #fff;
  .g-nav {
    padding: 0 0.7692rem;
    height: 4.1538rem;
    line-height: 4.1538rem;
    justify-content: space-between;
    padding-bottom: 0.7692rem;
    border-bottom: 0.0769rem solid #f1f1f1;
    span {
      font-size: 2rem;
      color: #999;
    }
    h3 {
      flex: 1;
      font-size: 1.3846rem;
      font-weight: 700;
      padding-left: 0.6rem;
    }
    a {
      padding: 0.3077rem 0.3077rem;
      font-size: 1.0769rem;
      color: #999;
    }
  }
  .hot-list {
    padding: 0 0.7692rem;
    .list-1 {
      width: 100%;
      height: 72.0005px;
      padding: 6px 0;
      padding-bottom: 0.7692rem;
      border-bottom: 0.0769rem solid #f1f1f1;
      justify-content: space-between;
      .hot-img {
        width: 12.3077rem;
        position: relative;
        img {
          display: block;
          width: 100%;
          height: 100%;
        }
        span {
          display: block;
          position: absolute;
          top: 50%;
          left: 50%;
          margin-left: -1.5385rem;
          margin-top: -1.5385rem;
          font-size: 3.0769rem;
          height: 3.0769rem;
          opacity: 0.6;
        }
      }
      .hot-title {
        padding-left: 0.6154rem;
        line-height: 1.6923rem;
        .hot-l {
          font-size: 1.1538rem;
          color: #343440;
        }
        .hot-r {
          color: #7a7a80;
          font-size: 0.9231rem;
          .iconfont {
            font-size: 0.7692rem;
          }
        }
      }
    }
    .list-2 {
      color: #343440;
      min-width: 0;
      font-size: 1rem;
      height: 2.7692rem;
      line-height: 2.7692rem;
      padding-bottom: 0.7692rem;
      border-bottom: 0.0769rem solid #f1f1f1;
      justify-content: space-between;
      .list-2-l {
        margin-left: 0.7692rem;
        color: #7a7a80;
        width: 6.1538rem;
        text-align: right;
        span {
          font-size: 0.9231rem !important;
          line-height: 0;
        }
      }
    }
  }
  .hot-btn {
    width: 100%;
    height: 2.6154rem;
    color: #7a7a80;
    background: #fff;
    text-align: center;
    line-height: 2.6154rem;
    border-top: 0.0769rem solid #f1f1f1;
  }
}
</style>


