<template>
    <div class="card padd">
      <div class="card-t flex">
        <span class="iconfont icon-information"></span>
        <h3>{{categorys.category_title}}</h3>
        <p class="iconfont icon-gengduo"></p>
      </div>  
      <div>
      <img src="../../static/images/heor.jpg" alt="" style="width:100%">
      </div>       
      <ul class="card-nav flex">
        <li @click="$refs.list.swiper.slideTo(index)" :class="{'b-active' : news_index===index}" v-for="(items,index) in categorys.news_list" :key="index">{{items.news_type}}</li>        
      </ul>
      <div class="card-news">             
        <swiper :options="{autoHeight:true,onSlideChangeEnd:function(){news_index = $refs.list.swiper.realIndex}}" ref="list" >
          <swiper-slide  v-for="(list,index) in categorys.news_list" :key="index">
            <div class="img-list flex">
               <a href="" v-for="(item,i) in list.type_news_list" :key="i">
                <img  class="imgs" :src="item.img_url" alt="" >
                <p>{{item.name}}</p>
               </a>                                
            </div>                  
          </swiper-slide> 
        </swiper>           
      </div>           
    </div> 
</template>
<script>
export default { 
  props:{    
    categorys:{type:Object},
  },  
  data() {
    return {     
        news_index:0, 
        active:0,                   
    }
    
  },  
  methods:{       
        changeIndex(){
          this.news_index=this.$refs.list.swiper.realIndex
        },
                   
  },
  computed:{
      
  },
  mounted() { 
   
  }
}
</script>
<style lang="scss" scoped>
  .card{
      margin-top:1.5385rem;
      padding-bottom: 1.3846rem;
      width: 100%;
      // height: 20.7692rem;      
      background: #fff; 
      .card-t{
        height:3.8462rem;
        line-height: 3.8462rem;
        justify-content:space-between;
        border-bottom:0.0769rem solid #f1f1f1;
        h3{
          flex: 1;
          text-align: left;
          font-size: 1.2308rem;
          color:#333;
          text-indent: 0.7692rem;
        }
        span{
          display: block;
          font-size: 1.1538rem;
        }
        p{
          font-size:1.5385rem;
        }
      }
      .card-nav{
        width: 100%;
        margin: 0.7692rem 0;
        height:1.8462rem;
        line-height: 1.8462rem;
        font-size:1rem;
        color:#333; 
        justify-content: space-between;   
      }
      .card-news{        
        width: 100%;
        color:#333;       
        // margin:0 -0.4615rem;         
        .img-list{
          width: 100%; 
          // justify-content: space-between;         
          flex-wrap: wrap;          
          margin:0 -0.2308rem ;              
          a{            
            width: 20%; 
            padding:0.1538rem 0.4615rem;         
            text-align: center;   
            line-height: 1.5385rem;         
            .imgs{              
              width: 100%;
            }
          }
        }     
      }
  }
</style>






