<template>  
  <div class="main-content">
    <Lunbo />
    <div class="nav-list">
      <div class="main-nav" ref="bscroll" v-show="nav_show">
        <div class="bscroll-container content" ref="content">
              <div class="nav-t">
            <span class="iconfont icon-mingpian"></span>
            <p>报料站</p>
              </div>
          <div class="nav-t">
            <span class="iconfont icon-shu"></span>
            <p>故事站</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-shoutidai"></span>
            <p>周边商城</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-information"></span>
            <p>体验服</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-guide2zhiyin"></span>
            <p>新人专区</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-zhuangshihuahuajuan"></span>
            <p>荣耀传承</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-shequ"></span>
            <p>同人社区</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-27"></span>
            <p>王者营地</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-weixin"></span>
            <p>公众号</p>
          </div>
          <div class="nav-t">
            <span class="iconfont icon-banben"></span>
            <p>版本介绍</p>
          </div>
        </div>     
      </div>     
      <div class="main-navs flex" ref="nav" v-show="!nav_show">
      <div class="nav-t flex">
        <span class="iconfont icon-mingpian"></span>
        <p>报料站</p>
      </div>
       <div class="nav-t flex">
        <span class="iconfont icon-shu"></span>
        <p>故事站</p>
      </div>
       <div class="nav-t flex">
        <span class="iconfont icon-shoutidai"></span>
        <p>周边商城</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-information"></span>
        <p>体验服</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-guide2zhiyin"></span>
        <p>新人专区</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-zhuangshihuahuajuan"></span>
        <p>荣耀传承</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-shequ"></span>
        <p>同人社区</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-27"></span>
        <p>王者营地</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-weixin"></span>
        <p>公众号</p>
      </div>
       <div class="nav-t flex">
         <span class="iconfont icon-banben"></span>
        <p>版本介绍</p>
      </div>
      </div>
      <div class="to-up flex" @click="toUp()" >
        <span class="iconfont icon-shouqi-copy" :class="nav_show ? 'span-active': ''"></span>
        <p>{{nav_show ? '展开':'收起'}}</p>
      </div>
    </div>
   <card-lists :categorys="news_list"></card-lists> 
   <hero-lists :categorys="heros_list"></hero-lists>  
  </div>  
</template>
<script>
import BScroll from "better-scroll";
import newsData from "../data/news.json"
import herosData from "../data/heros.json"
import Lunbo  from "../components/Lunbo.vue"
export default {
  data() {
    return {     
        nav_show:false,
        news_list:{} ,
        heros_list:{}                      
    }
    
  },  
  methods:{
        toUp(){
          this.nav_show = ! this.nav_show;
          if(this.nav_show){
            this.$nextTick(()=>{
              this._initScroll();
            })
          }               
        },
        _initScroll(){
           let bscrollDom = this.$refs.bscroll;
           this.aBScroll = new BScroll(bscrollDom,{scrollX:true})         
        },
               
  },
  computed:{
     
  },
  mounted() { 
    this.$nextTick(()=>{
       this.news_list=newsData[0]
       this.heros_list=herosData[0]
    })
      
  },
  components:{
    Lunbo
  }

}
</script>
<style lang="scss">
  .main-content{
    width: 100%;   
    .nav-list{
      width: 100%;     
      background: #fff; 
      .main-nav{
        width: 100%;        
        margin-top:0.7692rem; 
        overflow: hidden; 
        position: relative; 
        height: 5.3846rem;       
        .bscroll-container{     
          position:absolute; 
          // width: 100%;          
          height: 4.6154rem;
          font-size:1.0769rem;
          color:#333;  
          margin-bottom:0.7692rem;                  
          display: flex;        
          .nav-t{
            width: 8rem;
            // float: left;
            text-align: center;
            margin:0.7692rem 0;
            border-right: 0.0769rem solid #999;                  
            &:last-child{
              border:0;
            }         
            span{                       
              font-size:2.3077rem;
              animation: all 1s                
              }        
            p{
              opacity: 0.6;
            }        
          } 
        }      
      }
      .main-navs{
        width: 100%;             
        flex-wrap: wrap;       
        font-size:1.0769rem;
        color:#333;  
        margin-top:0.7692rem; 
        overflow: hidden;           
        .nav-t{
          text-align: center;
          width: 25%;              
          flex-direction:column;        
          justify-content:center;
          margin:0.7692rem 0;
          border-right: 0.0769rem solid #999;
          &:nth-child(4n){
            border:0;
          }         
          span{                       
              font-size:2.3077rem;
            }        
          p{
            opacity: 0.6;
          }        
        }
      }
      .to-up{
        background:#f9f9f9;
        height: 2.5385rem; 
        text-align: center;
        font-size: 1.0769rem;
        line-height:  2.5385rem;        
        justify-content:center;
      }
    }
    
    
}

</style>





