<template>
    <div class="swiper-img">
      <swiper :options="swiperOption" ref="mySwiper" >
        <!-- slides -->
        <swiper-slide><img src="../../static/images/lbt1.jpeg"></swiper-slide>
        <swiper-slide><img src="../../static/images/lbt2.jpeg"></swiper-slide>
        <swiper-slide><img src="../../static/images/lbt3.jpeg"></swiper-slide>        
        <!-- Optional controls -->
        <div class="swiper-pagination page"  slot="pagination"></div>       
      </swiper>
    </div>
</template>
<script>
export default {
    data(){
        return{
            swiperOption: {
                pagination: '.swiper-pagination',
                slidesPerView: 'auto',
                centeredSlides: true,
                paginationClickable: true, 
                autoplay : 5000,//可选选项，自动滑动
                loop : true,//可选选项，开启循环         
            }, 
        }
    },
}
</script>
<style lang="scss">
    .swiper-img {
        // height: 14rem;
        overflow: hidden; 
        // position: relative; 
        img {
            display: block;
            width: 100%;
            height: 100%;
        }
        .page{
            text-align: right; 
            padding-right:1.5385rem ;  
            .swiper-pagination-bullet{
            background: #fff;
            opacity: 1;
            border-radius: 0.1538rem;
            width: 0.7692rem;
            height: 0.7692rem;
            z-index: 10;      
            }
            .swiper-pagination-bullet-active{
            background: #4b67af;
            z-index: 10;
            }
        }
        
    }
</style>





